.wrapper{
    height: 71vh;
}
.user_info{
    display: flex;
    width: 700px;
    justify-content: space-around;
    margin: 0 auto;
    font-weight: 700;
    font-size: 24px;
    position: relative;
    bottom: 10px;
}
.user_personalData{
    display: flex;
}
.user_personalData div:first-child{
    padding: 0 10px 0 0;
}
.lines{
    display: flex;
}
.lines div{
    flex: 0 0 30%;
    padding: 0 30px 0 0;
}
.lines div:first-child{
    padding: 0 30px 0 15px;
}
.line1_description, .line2_description, .line3_description{
    height: 300px;
    border: 2px solid;
    border-radius: 10px;
    margin: 0 0 20px 0;
}
.line1_description{
    margin: 40px 0 0 0;
}
.line1_title{
    position: relative;
    top: 15px;
}
.line2_title, .line3_title{
    margin: 0 0 8px 0;
}
.line1_title, .line2_title, .line3_title{
    text-align: center;
    font-size: 24px;
    font-weight: 500;
}
.description_title{
    text-align: center;
    font-size: 18px;
    font-weight: 700;
}
.line1_btn button, .line2_btn button, .line3_btn button{
    width: 130px;
    height: 30px;
    border-radius: 10px;
    background-color: rgba(15, 167, 17, 0.492);
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1px;
}
.line1_btn, .line2_btn, .line3_btn{
    text-align: center;
}
.line1_btn{
    position: relative;
    top: 20px;
}
.line1{
    position: relative;
    left: 20px;
}
.line2{
    position: relative;
    left: 40px;
}
.line3{
    position: relative;
    left: 40px;
}
.line1_btn button:active, .line2_btn button:active, .line3_btn button:active{
    background-color: rgb(135, 13, 13);
    color: #fff;
}