.wrapper{
    background-color: #2F4F4F;
    height: 100px;
    display: flex;
    margin: 0 0 30px 0;
}
.header_logo img{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin: 13px 0 0 30px;
}
.header_items{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.items_log{
    display: flex;
    padding: 32px 0 0 0;
}
a{
    text-decoration: none;
}
.log_logOut a, .lines_info a, .user_info a{
    color: #fff;
    font-size: 20px;
}
.log_logOut{
    padding: 0 50px 0 0;
}
.user_info{
    position: relative;
    right: 30px;
}
.lines_info{
    position: relative;
    right: 60px;
}
.items_text{
    text-align: center;
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    position: relative;
    left: 587px;
    top: 30px;   
}
.log_logOut:hover a, .lines_info:hover a, .user_info:hover a{
    color: yellow;
}
