.wrapper{
    height: 71vh;
    text-align: center;
}
.line2_title{
    font-weight: 700;
    font-size: 32px;
    margin: 0 0 10px 0;
}
.line2_timer{
    font-weight: 700;
    font-size: 20px;
    margin: 0 0 30px 0;
}
.line2_data{
    display: inline-block;  
    border: 2px solid;
    position: relative;
    padding: 10px;
    top: 50px;
    border-radius: 10px;
}
.user_data div{
    display: inline-block;
    padding: 5px 20px;
}