.wrapper {
    height: 71vh;
    text-align: center;
}

.form_title {
    padding: 70px 0 0 0;
}

.error{
    color: red;
    margin: 0 0 5px 0;
}

.logIn_form{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 0 10px 0;
}

.logIn_form form {
    display: flex;
    flex-direction: column;
}

.logIn_form form input {
    width: 250px;
    height: 30px;
    border-radius: 10px;
    margin: 0 0 20px 0;
    font-size: 16px;
    padding: 0 0 0 5px;
}

.form_btn {
    width: 130px;
    height: 30px;
    border-radius: 10px;
    background-color: rgba(15, 167, 17, 0.492);
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 1px;
}

.form_btn:active {
    background-color: rgb(135, 13, 13);
    color: #fff;
}
