.wrapper{
    height: 71vh;
}
.title{
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    padding: 0 0 50px 0;
}
.information{
    display: flex;
    justify-content: space-around;
}
.information div{   
    flex: 0 0 25%;
    margin: 20px 0 0 0;
}
.information_tutorial__title, .information_student__title, .information_teacher__title{
    text-align: center;
    font-size: 24px;
    font-weight: 700;
} 
.information_tutorial__text, .information_student__text, .information_teacher__text{
    font-size: 20px;
    padding: 0 10px;
}
.information_tutorial, .information_student, .information_teacher{
    border: 2px solid;
    border-radius: 20px;
    height: 300px;
}