.wrapper {
    height: 71vh;
    text-align: center;
}

.signUp_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 0 10px 0;
}

.signUp_form form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.signUp_form form input {
    width: 250px;
    height: 30px;
    border-radius: 10px;
    margin: 0 0 20px 0;
    font-size: 16px;
    padding: 0 0 0 5px;
}

.error{
    margin: 0 0 5px 0;
    color: red;
}

.form_btn{
    width: 200px;
    height: 40px;
    border-radius: 10px;
    background-color: rgba(15, 167, 17, 0.492);
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 1px;
    border: 2px solid;
    color: #000;
    border-color: #000;
    position: relative;
}

.form_btn:active {
    background-color: rgb(135, 13, 13);
    color: #fff;
}