.wrapper{
    background-color: #2F4F4F;
    height: 100px;
    display: flex;
    margin: 0 0 30px 0;
}
.header_logo img{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin: 13px 0 0 30px;
}
.header_items{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.items_log{
    display: flex;
    padding: 32px 0 0 0;
}
a{
    text-decoration: none;
}
.log_logIn a, .log_signIn a{
    color: #fff;
    font-size: 20px;
}
.log_logIn{
    padding: 0 20px 0 0;
}
.log_signIn{
    padding: 0 20px 0 0;
}
.items_text{
    text-align: center;
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    position: relative;
    left: 587px;
    top: 30px;   
}
.log_signIn:hover a, .log_logIn:hover a{
    color: yellow;
}
