.wrapper{
    height: 71vh;
}
.lines{
    padding: 100px 0 0 0;
    display: flex;
    width: 1200px;
    margin: 0 auto;
    justify-content: space-between; 
}
.lines div{
    flex: 0 0 32%;
}
.line1, .line2, .line3{
    height: 200px;
    border: 2px solid;
    border-radius: 10px;
}
.line1_title, .line2_title, .line3_title{
    text-align: center;
    font-size: 24px;
    font-weight: 500; 
}
.line1_info, .line2_info, .line3_info{
    font-size: 40px;
    font-weight: 700;
    text-align: center;
    padding: 20px 0 0 0; 
}
.line1_info{
    position: relative;
    top: 35 px;
}